fastmap.mapApi.source.ClassicalSource = fastmap.mapApi.source.Source.extend({
    createParameter: function (tile) {
        var parameter = FM.Util.clone(this._requestParameter);
        parameter.x = tile.x;
        parameter.y = tile.y;
        parameter.z = tile.z;
        return parameter;
    }
});
