/**
 * Created by liwanchong on 2015/9/8.
 * lineString对象
 * @namespace fast.mapApi
 * @class LineString
 */
fastmap.mapApi.LineString = fastmap.mapApi.Collection.extend({
    /**
     * 几何类型
     * type
     * @property type
     * @type String
     */
    type: 'LineString',

    /**
     * @class LineString
     * @constructor
     * @namespace fastmap.mapApi
     * @param {Array}Points
     */
    initialize: function (points) {
        fastmap.mapApi.Collection.prototype.initialize.apply(this, arguments);
        this.points = points;
    },

    /**
     * 删除点
     * @method removeComponent
     * @return ｛boolean｝ 是否删除成功.
     */
    removeComponent: function (point) {
        var removed = this.components && (this.components.length > 2);
        if (removed) {
            fastmap.mapApi.Collection.prototype.removeComponent.apply(this,
                arguments);
        }
        return removed;
    },

    /**
     * 复制整个lineString
     * @method clone
     * @return LineString Clone.
     */
    clone: function () {
        var newpoints = [];
        newpoints = this.points.slice(0);
        var lineString = new fastmap.mapApi.LineString(newpoints);
        return lineString;
    },
    /**
     *获取lineString坐标数据
     * @method getCoordinates
     * @param {Array}coordinates
     */

    getCoordinates: function () {
    },
    /**
     * 获取开始点
     * @method getStartPoint
     * @param {Point}coordinates
     */
    getStartPoint: function (coordinates) {

    },
    /**
     * 获取线的结束点
     * @method getEndPoint
     * @param {Point} coordinates
     */
    getEndPoint: function (coordinates) {

    },
    /**
     *点到线的小片段的距离
     * @method pointToSegmentDistance
     * @param {Point}p
     * @param {Point}p1
     * @param {Point}p2
     * @returns {number}
     */
    pointToSegmentDistance: function (/* Point*/ p, /* Point*/ p1, /* Point*/ p2) {
        return this._sqClosestPointOnSegment(p, p1, p2);
    },

    _sqClosestPointOnSegment: function (p, p1, p2) {
        var x0 = p.x;
        var y0 = p.y;
        var x1 = p1.x;
        var y1 = p1.y;
        var x2 = p2.x;
        var y2 = p2.y;
        var dx = x2 - x1;
        var dy = y2 - y1;
        var along = ((dx * (x0 - x1)) + (dy * (y0 - y1))) /
            (Math.pow(dx, 2) + Math.pow(dy, 2));
        var x,
            y;
        if (along <= 0.0) {
            x = x1;
            y = y1;
        } else if (along >= 1.0) {
            x = x2;
            y = y2;
        } else {
            x = x1 + along * dx;
            y = y1 + along * dy;
        }
        return {
            distance: Math.sqrt(Math.pow(x - x0, 2) + Math.pow(y - y0, 2)),
            x: x,
            y: y,
            along: along
        };
    },
    /**
     *判断线是否交汇
     * @method intersects
     * @param {Geometry}geometry
     * @returns {boolean}
     */
    intersects: function (geometry) {
        var intersect = false;
        var type = geometry.type;
        if (type == 'LineString' ||
            type == 'LinearRing' ||
            type == 'Point') {
            var segs1 = this.getSortedSegments();
            var segs2;
            if (type == 'Point') {
                segs2 = [{
                    x1: geometry.x,
                    y1: geometry.y,
                    x2: geometry.x,
                    y2: geometry.y
                }];
            } else {
                segs2 = geometry.getSortedSegments();
            }
            var seg1,
                seg1x1,
                seg1x2,
                seg1y1,
                seg1y2,
                seg2,
                seg2y1,
                seg2y2;
            // sweep right
            outer: for (var i = 0, len = segs1.length; i < len; ++i) {
                seg1 = segs1[i];
                seg1x1 = seg1.x1;
                seg1x2 = seg1.x2;
                seg1y1 = seg1.y1;
                seg1y2 = seg1.y2;
                for (var j = 0, jlen = segs2.length; j < jlen; ++j) {
                    seg2 = segs2[j];
                    if (seg2.x1 > seg1x2) {
                        // seg1 still left of seg2
                        break;
                    }
                    if (seg2.x2 < seg1x1) {
                        // seg2 still left of seg1
                        continue;
                    }
                    seg2y1 = seg2.y1;
                    seg2y2 = seg2.y2;
                    if (Math.min(seg2y1, seg2y2) > Math.max(seg1y1, seg1y2)) {
                        // seg2 above seg1
                        continue;
                    }
                    if (Math.max(seg2y1, seg2y2) < Math.min(seg1y1, seg1y2)) {
                        // seg2 below seg1
                        continue;
                    }
                    if (fastmap.mapApi.Geometry.segmentsIntersect(seg1, seg2)) {
                        intersect = true;
                        break outer;
                    }
                }
            }
        } else {
            intersect = geometry.intersects(this);
        }
        return intersect;
    },
    /**
     * 获取组成线的片段
     *@method getSortedSegments
     * @param {Boolean}boolsort 是否返回排序的片段
     * Returns:
     * {Array} An array of segment objects.  Segment objects have properties
     *     x1, y1, x2, and y2.  The start point is represented by x1 and y1.
     *     The end point is represented by x2 and y2.  Start and end are
     *     ordered so that x1 < x2.
     */
    getSortedSegments: function (boolsort) {
        var numSeg = this.components.length - 1;
        var segments = new Array(numSeg);
        var point1,
            point2;
        for (var i = 0; i < numSeg; ++i) {
            point1 = this.components[i];
            point2 = this.components[i + 1];
            if (point1.x < point2.x) {
                segments[i] = {
                    x1: point1.x,
                    y1: point1.y,
                    x2: point2.x,
                    y2: point2.y
                };
            } else {
                segments[i] = {
                    x1: point2.x,
                    y1: point2.y,
                    x2: point1.x,
                    y2: point1.y
                };
            }
        }
        // more efficient to define this somewhere static
        function byX1(seg1, seg2) {
            return seg1.x1 - seg2.x1;
        }

        if (boolsort == true) {
            return segments.sort(byX1);
        }
        return segments;
    },

    getSegments: function () {
        var numSeg = this.components.length - 1;
        var segments = new Array(numSeg);
        var point1,
            point2;
        for (var i = 0; i < numSeg; ++i) {
            point1 = this.components[i];
            point2 = this.components[i + 1];
            segments[i] = {
                x1: point1.x,
                y1: point1.y,
                x2: point2.x,
                y2: point2.y
            };
        }
        return segments;
    },

    /**
     *把线分离成多个片段
     *@method splitWithSegment
     * @param {Object}seg
     * @param {Object}options
     */
    splitWithSegment: function (seg, options) {
        var edge = !(options && options.edge === false);
        var tolerance = options && options.tolerance;
        var lines = [];
        var verts = this.getVertices();
        var points = [];
        var intersections = [];
        var split = false;
        var vert1,
            vert2,
            point;
        var node,
            vertex,
            target;
        var interOptions = { point: true, tolerance: tolerance };
        var result = null;
        for (var i = 0, stop = verts.length - 2; i <= stop; ++i) {
            vert1 = verts[i];
            points.push(vert1.clone());
            vert2 = verts[i + 1];
            target = { x1: vert1.x, y1: vert1.y, x2: vert2.x, y2: vert2.y };
            point = fastmap.mapApi.Geometry.segmentsIntersect(
                seg, target, interOptions
            );
            if (point instanceof fastmap.mapApi.Point) {
                if ((point.x === seg.x1 && point.y === seg.y1) ||
                    (point.x === seg.x2 && point.y === seg.y2) ||
                    point.equals(vert1) || point.equals(vert2)) {
                    vertex = true;
                } else {
                    vertex = false;
                }
                if (vertex || edge) {
                    // push intersections different than the previous
                    if (!point.equals(intersections[intersections.length - 1])) {
                        intersections.push(point.clone());
                    }
                    if (i === 0) {
                        if (point.equals(vert1)) {
                            continue;
                        }
                    }
                    if (point.equals(vert2)) {
                        continue;
                    }
                    split = true;
                    if (!point.equals(vert1)) {
                        points.push(point);
                    }
                    lines.push(new fastmap.mapApi.LineString(points));
                    points = [point.clone()];
                }
            }
        }
        if (split) {
            points.push(vert2.clone());
            lines.push(new fastmap.mapApi.LineString(points));
        }
        if (intersections.length > 0) {
            // sort intersections along segment
            var xDir = seg.x1 < seg.x2 ? 1 : -1;
            var yDir = seg.y1 < seg.y2 ? 1 : -1;
            result = {
                lines: lines,
                points: intersections.sort(function (p1, p2) {
                    return (xDir * p1.x - xDir * p2.x) || (yDir * p1.y - yDir * p2.y);
                })
            };
        }
        return result;
    },
    /**
     * 根据参数分离geometry
     *@method split
     * @param {Object}target
     * @param {Object}options
     */
    split: function (target, options) {
        var results = null;
        var mutual = options && options.mutual;
        var sourceSplit,
            targetSplit,
            sourceParts,
            targetParts;
        if (target instanceof fastmap.mapApi.LineString) {
            var verts = this.getVertices();
            var vert1,
                vert2,
                seg,
                splits,
                lines,
                point;
            var points = [];
            sourceParts = [];
            for (var i = 0, stop = verts.length - 2; i <= stop; ++i) {
                vert1 = verts[i];
                vert2 = verts[i + 1];
                seg = {
                    x1: vert1.x,
                    y1: vert1.y,
                    x2: vert2.x,
                    y2: vert2.y
                };
                targetParts = targetParts || [target];
                if (mutual) {
                    points.push(vert1.clone());
                }
                for (var j = 0; j < targetParts.length; ++j) {
                    splits = targetParts[j].splitWithSegment(seg, options);
                    if (splits) {
                        // splice in new features
                        lines = splits.lines;
                        if (lines.length > 0) {
                            lines.unshift(j, 1);
                            Array.prototype.splice.apply(targetParts, lines);
                            j += lines.length - 2;
                        }
                        if (mutual) {
                            for (var k = 0, len = splits.points.length; k < len; ++k) {
                                point = splits.points[k];
                                if (!point.equals(vert1)) {
                                    points.push(point);
                                    sourceParts.push(new fastmap.mapApi.LineString(points));
                                    if (point.equals(vert2)) {
                                        points = [];
                                    } else {
                                        points = [point.clone()];
                                    }
                                }
                            }
                        }
                    }
                }
            }
            if (mutual && sourceParts.length > 0 && points.length > 0) {
                points.push(vert2.clone());
                sourceParts.push(new fastmap.mapApi.LineString(points));
            }
        } else {
            results = target.splitWith(this, options);
        }
        if (targetParts && targetParts.length > 1) {
            targetSplit = true;
        } else {
            targetParts = [];
        }
        if (sourceParts && sourceParts.length > 1) {
            sourceSplit = true;
        } else {
            sourceParts = [];
        }
        if (targetSplit || sourceSplit) {
            if (mutual) {
                results = [sourceParts, targetParts];
            } else {
                results = targetParts;
            }
        }
        return results;
    },

    splitWith: function (geometry, options) {
        return geometry.split(this, options);
    },

    /**
     * 距线最近的点
     * @method distanceTo
     * @param {Geometry}geometry
     * @param {Object}options
     * @returns {{}}
     */
    distanceTo: function (geometry, options) {
        var edge = !(options && options.edge === false);
        var details = edge && options && options.details;
        var result;
        var best = {};
        var min = Number.POSITIVE_INFINITY;
        var i;
        if (geometry instanceof fastmap.mapApi.Point) {
            var segs = this.getSortedSegments();
            var x = geometry.x;
            var y = geometry.y;
            var seg;
            for (i = 0; i < segs.length; ++i) {
                seg = segs[i];
                result = fastmap.mapApi.Geometry.distanceToSegment(geometry, seg);
                if (result.distance < min) {
                    min = result.distance;
                    best = result;
                    if (min === 0) {
                        break;
                    }
                } else if (seg.x2 > x && ((y > seg.y1 && y < seg.y2) || (y < seg.y1 && y > seg.y2))) {
                    // if distance increases and we cross y0 to the right of x0, no need to keep looking.
                    break;
                }
            }
            if (details) {
                best = {
                    distance: best.distance,
                    x0: best.x,
                    y0: best.y,
                    x1: x,
                    y1: y
                };
            } else {
                best = best.distance;
            }
        } else if (geometry instanceof fastmap.mapApi.LineString) {
            var segs0 = this.getSortedSegments();
            var segs1 = geometry.getSortedSegments();
            var seg0,
                seg1,
                intersection,
                x0,
                y0;
            var len1 = segs1.length;
            var interOptions = { point: true };
            outer: for (i = 0; i < segs0.length; ++i) {
                seg0 = segs0[i];
                x0 = seg0.x1;
                y0 = seg0.y1;
                for (var j = 0; j < len1; ++j) {
                    seg1 = segs1[j];
                    intersection = fastmap.mapApi.Geometry.segmentsIntersect(seg0, seg1, interOptions);
                    if (intersection) {
                        min = 0;
                        best = {
                            distance: 0,
                            x0: intersection.x,
                            y0: intersection.y,
                            x1: intersection.x,
                            y1: intersection.y
                        };
                        break outer;
                    } else {
                        result = fastmap.mapApi.Geometry.distanceToSegment({ x: x0, y: y0 }, seg1);
                        if (result.distance < min) {
                            min = result.distance;
                            best = {
                                distance: min,
                                x0: x0,
                                y0: y0,
                                x1: result.x,
                                y1: result.y
                            };
                        }
                    }
                }
            }
            if (!details) {
                best = best.distance;
            }
            if (min !== 0) {
                // check the final vertex in this line's sorted segments
                if (seg0) {
                    result = geometry.distanceTo(
                        new fastmap.mapApi.Point(seg0.x2, seg0.y2),
                        options
                    );
                    var dist = details ? result.distance : result;
                    if (dist < min) {
                        if (details) {
                            best = {
                                distance: min,
                                x0: result.x1,
                                y0: result.y1,
                                x1: result.x0,
                                y1: result.y0
                            };
                        } else {
                            best = dist;
                        }
                    }
                }
            }
        } else {
            best = geometry.distanceTo(this, options);
            // swap since target comes from this line
            if (details) {
                best = {
                    distance: best.distance,
                    x0: best.x1,
                    y0: best.y1,
                    x1: best.x0,
                    y1: best.y0
                };
            }
        }
        return best;
    },

    getVertices: function (nodes) {
        var vertices;
        if (nodes === true) {
            vertices = [
                this.components[0],
                this.components[this.components.length - 1]
            ];
        } else if (nodes === false) {
            vertices = this.components.slice(1, this.components.length - 1);
        } else {
            vertices = this.components.slice();
        }
        return vertices;
    },

    /**
     *根据容差获取数据
     * @method simplify
     * @param {Number}tolerance
     */
    simplify: function (tolerance) {
        if (this && this !== null) {
            var points = this.getVertices();
            if (points.length < 3) {
                return this;
            }
            var compareNumbers = function (a, b) {
                return (a - b);
            };
            var firstPoint = 0;
            var lastPoint = points.length - 1;
            var pointIndexsToKeep = [];

            // Add the first and last index to the keepers
            pointIndexsToKeep.push(firstPoint);
            pointIndexsToKeep.push(lastPoint);

            // The first and the last point cannot be the same
            while (points[firstPoint].equals(points[lastPoint])) {
                lastPoint--;
                // Addition: the first point not equal to first point in the LineString is kept as well
                pointIndexsToKeep.push(lastPoint);
            }

            /**
             * Private function calculating the perpendicular distance
             * TODO: check whether OpenLayers.Geometry.LineString::distanceTo() is faster or slower
             */
            var perpendicularDistance = function (point1, point2, point) {
                // Area = |(1/2)(x1y2 + x2y3 + x3y1 - x2y1 - x3y2 - x1y3)|   *Area of triangle
                // Base = v((x1-x2)²+(x1-x2)²)                               *Base of Triangle*
                // Area = .5*Base*H                                          *Solve for height
                // Height = Area/.5/Base

                var area = Math.abs(0.5 * (point1.x * point2.y + point2.x * point.y + point.x * point1.y - point2.x * point1.y - point.x * point2.y - point1.x * point.y));
                var bottom = Math.sqrt(Math.pow(point1.x - point2.x, 2) + Math.pow(point1.y - point2.y, 2));
                var height = area / bottom * 2;

                return height;
            };
            /**
             * Private function doing the Douglas-Peucker reduction
             */
            var douglasPeuckerReduction = function (points1, firstPoint1, lastPoint1, tolerance1) {
                var maxDistance = 0;
                var indexFarthest = 0;

                for (var index = firstPoint1, distance; index < lastPoint1; index++) {
                    distance = perpendicularDistance(points1[firstPoint1], points1[lastPoint1], points1[index]);
                    if (distance > maxDistance) {
                        maxDistance = distance;
                        indexFarthest = index;
                    }
                }

                if (maxDistance > tolerance1 && indexFarthest != firstPoint1) {
                    // Add the largest point that exceeds the tolerance1
                    pointIndexsToKeep.push(indexFarthest);
                    douglasPeuckerReduction(points1, firstPoint1, indexFarthest, tolerance1);
                    douglasPeuckerReduction(points1, indexFarthest, lastPoint1, tolerance1);
                }
            };
            douglasPeuckerReduction(points, firstPoint, lastPoint, tolerance);
            var returnPoints = [];
            pointIndexsToKeep.sort(compareNumbers);
            for (var index = 0; index < pointIndexsToKeep.length; index++) {
                returnPoints.push(points[pointIndexsToKeep[index]]);
            }
            // return new OpenLayers.Geometry.LineString(returnPoints);
            return new FM.Geometry.LineString(returnPoints);
        }
        return this;
    }
});
fastmap.mapApi.lineString = function (coordiates) {
    return new fastmap.mapApi.LineString(coordiates);
};

